.page-header {
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  width: 100%;
  color: $white;
  background-color: $dark-grey;
  background-image: url("../img/image-background.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center; }

.page-header__container {
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  padding-top: 40px;
  max-width: 1411px;
  width: 100%; }

.nav-wrapper {
  display: flex;
  width: 100%; }

.page-header__name {
  margin: 0;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  color: $white;
  text-decoration: none; }

.page-header__text {
  margin: 0;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  color: $light-grey;
  text-decoration: none; }

.contacts-list__item {
  display: flex;
  flex-direction: row;
  margin: 0;
  margin-left: 3px;
  margin-right: 20px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px; }

.link {
  display: block;
  text-decoration: none;
  color: $white;
  margin-left: auto;

  svg {
    transform: translate(-50%, 10%); }

  &:hover {
    // color: $blue
    opacity: 0.6;


    svg {
      opacity: 0.6; } } }

.contacts-list__item--email {
  display: flex;
  width: 140px;
  text-decoration: none;
  margin-right: 60px;
  margin-top: 15px;
  color: $white;

  svg {
    width: 19px;
    height: 14px;
    fill: $blue; } }


.contacts-list__item--phone {
  width: 207px;
  display: flex;
  margin-top: 15px;

  svg {
    width: 18px;
    height: 18px;
    fill: $blue; } }

.nav-wrapper__logo {
  display: flex;
  width: auto;
  svg {
    fill: $blue;
    width: 52px;
    height: 52px; } }

.page-header__logo {
  display: flex;
  padding-left: 10px;
  text-decoration: none; }

.page-header__wrapper {
  display: flex;
  flex-direction: column;
  margin-left: 16px; }

.contacts-list {
  display: flex;
  margin: 0;
  margin-left: auto;
  padding: 0;
  width: auto;
  list-style: none; }

.page-header__title {
  margin: 0;
  margin-bottom: 30px;
  z-index: 2;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase; }

.about__info {
  position: relative;
  margin: 0;
  margin-bottom: 40px;
  padding-left: 100px;
  width: 76%;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px; }

.about__info::before {
  position: absolute;
  content: "";
  display: block;
  width: 66px;
  height: 2px;
  left: 20px;
  top: 30px;
  color: $white;
  background-color: $white; }

.about {
  // position: relative
  display: flex;
  flex-direction: column;
  padding-bottom: 278px;
  padding-top: 230px;
  padding-left: 50px; }

.about-wrapper {
  display: flex;
  max-width: 1447px;
  width: 100%;
  margin-left: auto;
  margin-right: auto; }

.about-scroll {
  position: absolute;
  display: block;
  transform: rotate(-90deg);
  left: 10px;
  bottom: 100px;
  color: $white;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
  opacity: 1;
  transition: opacity;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  transition-delay: 0s;

  &:hover,
  &:active,
  &:focus {
    // color: $blue
    opacity: 0.7; } }

.page-header__burger {
  position: relative; }

.about-picture {
  position: absolute;
  left: 750px;
  width: 705px;
  height: 515px;
  background-image: url("../img/polygons.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  font-size: 0; }

.about-picture__img {
  width: 705px;
  height: 515px; }

@media screen and (max-width: 1023px) {
  .page-header {
    max-width: none;
    width: 100%; }

  .page-header__container {
    max-width: 700px;
    width: 100%; }

  .page-header__title {
    font-size: 40px;
    line-height: 47px;
    letter-spacing: 0.07em; }

  .about__info {
    font-size: 18px;
    line-height: 28px; }

  .about-scroll {
    display: none; }

  .about-picture {
    position: absolute;
    right: 0;
    left: auto;
    width: 300px;
    height: 340px;
    background-size: cover; }

  .desktop-only {
    display: none; }

  .contacts-list__item {
    width: 30px; }

  .contacts-list__item--phone {
    svg {
      width: 24px;
      height: 24px; } }

  .contacts-list__item--email {
    margin-right: 10px;
    svg {
      width: 26px;
      height: 19px; } } }


@media screen and (max-width: 767px) {
  .page-header__container {
    margin-right: auto;
    max-width: 300px;
    width: 100%; }

  .about {
    padding-left: 10px; }

  .about__info {
    width: 295px;
    font-size: 16px;
    line-height: 25px;
    padding-left: 60px; }

  .page-header__title {
    width: 277px; }

  .contacts-list {
    width: auto; }

  .about-picture__img {
    overflow: hidden;
    right: 0;
    left: auto;
    top: 70px;
    width: 301px;
    height: 220px; }

  .about-picture {
    position: absolute;
    top: 100px;
    // left: 20px
    background-image: url("../img/polygons.png");
    background-repeat: no-repeat;
    background-position: top left;
    background-size: 100% 100%; }

  .about__info::before {
    width: 33px; }

  .mobile-hide {
    display: none; } }

