.company-offer {
  padding-top: 93px;
  padding-bottom: 110px;
  background-color: $dark-grey;
  color: $white;

  .page-main__wrapper {
    padding-left: 15px;
    padding-right: 15px; } }

.company-offer__title {
  margin: 0;
  margin-bottom: 47px;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 51px; }

.company-offer__name {
  margin: 0;
  margin-top: 50px;
  margin-left: 50px;
  width: 50%;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 30px;
  text-decoration: none; }

.company-offer__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  width: 100%;
  margin: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0; }

.company-offer__item {
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-bottom: 20px;
  width: calc(33% - 20px);
  min-height: 620px;
  text-decoration: none;
  color: $white;
  opacity: 1;
  transition: opacity;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
  transition-delay: 0s;
  overflow: hidden;

  &:first-child,
  &:nth-child(2n) {
    margin-right: 30px; }

  &:hover {
    opacity: 0.5; } }

.company-offer__button {
  position: relative;
  display: block;
  margin-left: auto;
  margin-top: auto;
  width: 63px;
  height: 63px;
  background-color: $blue; }

.company-offer__vector {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%; }

@media screen and (max-width: 1023px) {
  .company-offer {
    max-width: none;
    width: 100%; }

  .company-offer__list {
    flex-direction: column; }

  .company-offer__item {
    width: 100%;
    min-height: 256px;

    &:first-child,
    &:nth-child(2n) {
      margin-right: 0;
      margin-bottom: 45px; } }

  .company-offer__title {
    font-size: 32px;
    line-height: 45px; }

  .company-offer__item--production {
    background-image: url("img/production-tablet.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; }

  .company-offer__item--install {
    background-image: url("img/install-tablet.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; }

  .company-offer__item--additionals {
    background-image: url("img/additionals-tablet.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center; }

  .company-offer {
    padding-top: 45px;
    padding-bottom: 70px; }

  .company-offer__name {
    width: 25%; } }

@media screen and (max-width: 767px) {
  .company-offer__title {
    font-size: 28px;
    line-height: 39px; }

  .company-offer__item {
    width: 100%;
    min-height: 256px;

    &:first-child,
    &:nth-child(2n) {
      margin-bottom: 20px; } }

  .company-offer {
    padding-top: 53px;
    padding-bottom: 64px; }

  .company-offer__name {
    margin-left: 30px;
    margin-top: 20px;
    width: 60%; } }

